@import url('https://fonts.googleapis.com/css2?family=Kaushan+Script&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Varela+Round&display=swap');

.kaushan{
    font-family: 'Kaushan Script', cursive;
}
.poppins{
    font-family: 'Poppins', sans-serif;
}
.varela_round{
    font-family: 'Varela Round', sans-serif;
}

*{
    padding: 0;
    margin: 0;
}

textarea {
    width: 100%;
    height: 40vh;
    padding: 10px;
}
.mb3{
    margin-top: 10% !important;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
h1{

    text-align: center;
}
.hover{
    margin-top: 25px !important;
    width: 15rem;
    height: 3rem;
    transition: 0.2s linear;
}

.hover:hover{
    font-size: 18px;
}
.container{
    margin-top: 15px;
}
.main{
    border-radius: none !Important;
}
.accordion-header{
    border-radius: 10%;
}
body{
    height: 90vh;
}